<template>
  <v-container grid-list-md>
    <v-card>
    <v-card-title>
      Заказы
      <v-layout row>
        <v-dialog v-model="dialog" max-width="800px" persistent>
          <template v-slot:activator="{on}">
              <v-btn icon v-on="on"><v-icon>mdi-plus-circle-outline</v-icon></v-btn>
          </template>
          <v-card>
            <v-card-title>
              <span class="headline">Добавление заказа</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs4>
                    <v-select label="Клиент*" :items="['Олегов Олег']" required></v-select>
                  </v-flex>
                  <v-flex xs4>
                    <v-select label="Услуга*" :items="['Шлепки в масле']" required></v-select>
                  </v-flex>
                  <v-flex xs4>
                    <v-text-field required label="Количество*"></v-text-field>
                  </v-flex>
                </v-layout>
              </v-container>
              <small>* обязательные поля</small>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="dialog = false">Закрыть</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-layout>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Поиск"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="orders"
      :search="search"
      :items-per-page="15"
      :page.sync="page"
      hide-default-footer
      @page-count="pageCount = $event"
    />
    <v-card-actions>
      <v-pagination v-model="page" :length="pageCount"></v-pagination>
    </v-card-actions>
  </v-card>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    page: 1,
    pageCount: 0,
    search: '',
    headers: [
      {text: 'Клиент', value: 'client', align: 'center'},
      {text: 'Номер', value: 'phone', align: 'center', sortable: false},
      {text: 'Заказ', value: 'order', align: 'center', sortable: false},
      {text: 'Кол-во (ед)', value: 'count', align: 'center'},
      {text: 'Сумма (руб)', value: 'amount', align: 'center'}
    ],
    orders: [
      {client: 'Олегов Олег', phone: '+78888888888', order: 'Шлепки в масле', count: '1', amount: '12500'}
    ]
  })
}
</script>
